.myInfo {
   padding: 10px;
   border-radius: 5px;
   font-size: 14px;
   margin-bottom: 10px;
}

.myInfo-warning {
   background-color: #ffeeba;
   color: #856404;
   border: 1px solid #ffeeba;
}

.myInfo-error {
   background-color: #f8d7da;
   color: #721c24;
   border: 1px solid #f8d7da;
}
